import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { SearchModule } from 'src/app/common-module/search/search.module';
import { BtnAddModule } from 'src/app/common-module/btn-add/btn-add.module';
import { TableListModule } from 'src/app/common-module/table-list/table-list.module';
import { SharedModule } from 'src/app/shared/shared.module';
import { FormValidationModule } from 'src/app/shared/form-validation/form-validation.module';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { PaginationModule } from 'src/app/common-module/pagination/pagination.module';
import { NgSelectModule } from '@ng-select/ng-select';
import { WarehouseListComponent } from './warehouse-list/warehouse-list.component';
import { WarehouseService } from 'src/app/service/warehouse.service';
import { AddWarehouseComponent } from './add-warehouse/add-warehouse.component';

const routes: Routes = [
  {
    path: "",
    component: WarehouseListComponent,
    data: { title: 'warehouse_list' }
  },
  {
    path: "add",
    component: AddWarehouseComponent,
    data: { title: 'add_warehouse' }
  },
  {
    path: 'edit/:id',
    component: AddWarehouseComponent,
    data: { title: 'edit_warehouse' }
  },
]

@NgModule({
  declarations: [WarehouseListComponent, AddWarehouseComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    TableListModule,
    SearchModule,
    BtnAddModule,
    FormsModule,
    ReactiveFormsModule,
    FormValidationModule,
    PaginationModule,
    SharedModule,
    NgSelectModule
  ], providers: [WarehouseService]
})
export class WarehouseModule { }
